import React, { FC } from 'react';
import { createUseStyles } from 'react-jss';

import ProgressiveBar from './ProgressiveBar';

const useStyles = createUseStyles((theme: any) => ({
    items: {
        composes: 'flexColumn stretchSelf center blue',
        paddingLeft: theme.spacing(8),
        paddingRight: theme.spacing(8),
    },
    item: {
        composes: 'flexRow center stretchSelf red',
        marginBottom: theme.spacing(2)
    },
    texts: {
        composes: 'flexColumn stretchSelf center',
        flex: 1,
    },
    label: {

    },
    value: {

    },
    progressiveBar: {
        flex: 2,
    }
}));

type Props = { items: any };
const ItemsChart: FC<Props> = ({ items }) => {
    console.log('items: ', items);
    const classes = useStyles();
    return (
        <div className={classes.items}>
            {items.map((item, index) => (
                <div className={classes.item} key={index}>
                    <div className={classes.texts}>
                        <div className={classes.label}>
                            {item.label}
                        </div>
                        <div className={classes.value}>
                            {item.value}/100
                        </div>
                    </div>
                    <ProgressiveBar color={item.color} percent={item.value} width={167} className={classes.progressiveBar} />
                    {/* <ProgressiveBar color="#FD7A8C" percent={10} width={167} /> */}
                </div>
            ))}
        </div>
    );
};

export default ItemsChart;
